body {
  height: 100vh;
  background-color: #f2f2f2;
  justify-content: center;
  align-items: center;
  font-family: Arial, sans-serif;
  display: flex;
}

li {
  list-style: none;
}

a {
  text-decoration: none;
}

p {
  position: fixed;
  top: 20px;
  left: 20px;
}

button[type="button"] {
  color: #fff;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  padding: 10px;
  font-size: 16px;
  transition: all .2s;
}

button[type="button"][data-start] {
  background-color: #007bff;
}

button[type="button"][data-start]:hover {
  background-color: #0062cc;
}

button[type="button"][data-stop] {
  background-color: #dc3545;
  margin-left: 10px;
}

button[type="button"][data-stop]:hover {
  background-color: #c82333;
}

